import React from 'react';
import '../style/page/add.css'

const Add = () => (
    <div className='add'>
        <form action="">
            <div className="add__wrapper">
                <div className="add__block">
                    <h3 className="h3">English</h3>
                    <input type="text" placeholder='Write word'/>
                </div>
                <div className="add__block">
                    <h3 className="h3">Russia</h3>
                    <input type="text" placeholder='Translate word'/>
                </div>
            </div>
            <button className='add__btn'>Add word</button>
        </form>
    </div>
);

export default Add;


